import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./page/homePage/Home";
import Intro from "./page/introPage/Intro";
import Area from "./page/introPage/Area";
import History  from "./page/introPage/History";
import Notices from "./page/noticesPage/Notices";
import Site from "./page/sitePage/Site";
import Food from "./page/foodPage/Food";
import Router_c from "./component/router";
import ReactGA from 'react-ga';
import React, { useEffect } from "react";


export default function App() { 

  useEffect(() => {
    ReactGA.initialize('G-GRDDHK5X64');
    ReactGA.pageview(window.location.pathname+window.location.search);
  },[])

    return (
      <Router>
        <div className="App">
        <Router_c/>
          <Switch>
            <Route path="/reactpage" exact component={Home} />
            <Route path="/reactpage/intro" exact component={Intro} />
            <Route path="/reactpage/notices" component={Notices} />
            <Route path="/reactpage/site" component={Site} />
            <Route path="/reactpage/food" component={Food} />
            <Route path ="/reactpage/intro/area" component={Area}/>
            <Route path ="/reactpage/intro/history" component={History}/>
          </Switch>
        </div>
      </Router>
    );
}
