import axios from 'axios';
import React from 'react';

import '../../App.css';

export default class PictureList extends React.Component {

    state = {
        pictures:[],
    };
    
    componentDidMount() {
        axios.get('https://damp-woodland-59246.herokuapp.com/api/pictures/')
            .then(res => {
                console.log(res);
                this.setState({pictures:res.data});
            })
            .catch(err => {
                console.log(err);
            });
    }
    
    render() {
        
        return(
            <div className="api-root">
                {this.state.pictures.map(picture => (    
                    <img className="api-img"  src={picture.p_url} alt={picture.title}/>
                ))}
                
            </div>
        )
    }


}