import React from "react";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import ImageAvatars from "./Avatar";

export default function Router_c() {
  return (
    <nav>
      
      <ImageAvatars/>
      <h1>Welcome to Egypt</h1>
      <div className="links">
        <Link to="/reactpage">
          <Button>首頁</Button>
        </Link>
        <Link to="/reactpage/Intro">
          <Button>埃及介紹</Button>
        </Link>
        <Link to="/reactpage/site">
          <Button>景點</Button>
        </Link>
        <Link to="/reactpage/food">
          <Button>特色美食</Button>
        </Link>
        <Link to="/reactpage/notices">
          <Button>注意事項</Button>
        </Link>
      </div>
    </nav>
  );
}
